.homelist-ctn {
  max-width: 100vw;
  margin: 0;
  padding: 10vh 12vw;
  overflow: hidden;
}

.homelist-ctn h3 {
  text-align: center;
  font-size: 1rem;
}

.homelist-ctn > h3 {
  color: #df102a;
  font-size: 1.2rem;
}

.homelist-ctn h3 span {
  display: block;
  font-size: 2.5rem;
  color: var(--primary);
}

.cards-grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: auto auto auto;
  margin-top: 1.5rem;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #353535;
  font-size: 1rem;
  color: #fff;
  height: 100%;
  width: 100%;
  border-radius: 4px;
  transition: all 500ms;
  overflow: hidden;
  text-align: center;
}

.card img {
  transition: all 500ms;
  max-width: 100%;
  height: 50%;
}

.card img:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.card .info {
  width: 80%;
  height: 50%;
  text-align: justify;
  padding: 1rem 0;
}

.card .info-icon {
  font-size: 1.5rem;
}

.card .info h5 span {
  font-size: 1rem;
  margin-left: 8rem;
}

.card .info h5 .proprio {
  margin-left: 1rem;
}

.card .info-icon-text {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 1200px) {
  .homelist-ctn {
    padding: 10vh 7vw;
  }
  .cards-grid {
    gap: 1rem;
    margin-top: 1rem;
  }
  .card {
    font-size: 0.7rem;
  }

  .card .info-icon {
    font-size: 1rem;
  }
  .card .info h5 span {
    font-size: 1rem;
    margin-left: 1rem;
  }
}

@media only screen and (max-width: 992px) {
  .cards-grid {
    grid-template-columns: auto auto;
  }
  .card img {
    width: 100%;
    object-fit: cover;
  }
}

@media only screen and (max-width: 768px) {
  .cards-grid {
    grid-template-columns: auto;
  }
  .card .info {
    width: 70%;
    margin: 3rem 0;
  }
}
